                  <span>{{ getFieldValue(reportToDelete, 'place', 'lokasi', 'location') }}</span>
                </div>
              </div>
            </div>
            <div class="flex-shrink-0">
              <span 
                :class="getDetailStatusClass(getFieldValue(reportToDelete, 'status', 'state'))"
                class="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium"
              >
                {{ getFieldValue(reportToDelete, 'status', 'state') }}
              </span>
            </div>
          </div>
        </div>

        <!-- Confirmation Text -->
        <div class="bg-gray-50 border border-gray-200 rounded-lg p-4 mb-6">
          <div class="flex items-start space-x-3">
            <Icon icon="mdi:information-outline" class="w-5 h-5 text-blue-600 mt-0.5 flex-shrink-0" />
            <div class="text-sm text-gray-700">
              <p class="font-medium mb-1">Yang akan dihapus:</p>
              <ul class="list-disc list-inside space-y-1 text-xs">
                <li>Data laporan dan semua informasinya</li>
                <li>Lampiran dan bukti pendukung</li>
                <li>Riwayat aktivitas terkait laporan</li>
                <li>Komentar dan feedback</li>
              </ul>
            </div>
          </div>
        </div>

        <!-- Final Confirmation -->
        <div class="text-center">
          <p class="text-sm font-medium text-gray-900 mb-2">
            Apakah Anda yakin ingin menghapus laporan ini?
          </p>
          <p class="text-xs text-gray-600">
            Ketik "HAPUS" di bawah untuk mengkonfirmasi penghapusan
          </p>
          <el-input
            v-model="deleteConfirmationText"
            placeholder="Ketik HAPUS untuk konfirmasi"
            class="mt-3"
            size="small"
            maxlength="5"
            show-word-limit
          ></el-input>
        </div>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDeleteModal" :disabled="deleteLoading">
          Batal
        </el-button>
        <el-button 
          type="danger" 
          @click="confirmDeleteReport" 
          :loading="deleteLoading"
          :disabled="deleteConfirmationText !== 'HAPUS'"
          icon="el-icon-delete"
        >
          {{ deleteLoading ? 'Menghapus...' : 'Ya, Hapus Laporan' }}
        </el-button>
      </div>
    </el-dialog>

    <!-- Status Update Modal -->
    <el-dialog
      :visible.sync="showStatusModal"
      :close-on-click-modal="false"
      :close-on-press-escape="true"
      width="40%"
      custom-class="status-update-modal"
      @close="closeStatusModal"
      top="20px"
    >
      <div slot="title" class="flex items-center">
        <Icon icon="mdi:clipboard-edit" class="mr-2 text-orange-600" />
        <span class="text-lg font-semibold">Update Status Laporan</span>
      </div>

      <div v-if="reportToUpdateStatus" class="status-modal-content">
        <!-- Report Info Card -->
        <div class="bg-gradient-to-r from-orange-50 to-orange-100 border border-orange-200 rounded-lg p-4 mb-6">
          <div class="flex items-start space-x-3">
            <div class="w-10 h-10 bg-orange-500 rounded-full flex items-center justify-center flex-shrink-0">
              <Icon icon="mdi:file-document" class="w-5 h-5 text-white" />
            </div>
            <div class="flex-1 min-w-0">
              <h4 class="text-sm font-medium text-orange-900 mb-1">
                {{ getFieldValue(reportToUpdateStatus, 'name', 'nama', 'title') }}
              </h4>
              <div class="space-y-1">
                <div v-if="getFieldValue(reportToUpdateStatus, 'code', 'kode') !== '-'" class="flex items-center text-xs text-orange-700">
                  <Icon icon="mdi:bookmark-outline" class="w-3 h-3 mr-1" />
                  <span>{{ getFieldValue(reportToUpdateStatus, 'code', 'kode') }}</span>
                </div>
                <div class="flex items-center text-xs text-orange-700">
                  <Icon icon="mdi:account" class="w-3 h-3 mr-1" />
                  <span class="font-mono">{{ reportToUpdateStatus.createdBy }}</span>
                </div>
                <div class="flex items-center text-xs text-orange-700">
                  <Icon icon="mdi:map-marker" class="w-3 h-3 mr-1" />
                  <span>{{ getFieldValue(reportToUpdateStatus, 'place', 'lokasi', 'location') }}</span>
                </div>
              </div>
            </div>
            <div class="flex-shrink-0">
              <span 
                :class="getDetailStatusClass(getFieldValue(reportToUpdateStatus, 'status', 'state'))"
                class="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium"
              >
                {{ getFieldValue(reportToUpdateStatus, 'status', 'state') }}
              </span>
            </div>
          </div>
        </div>

        <!-- Status Selection Form -->
        <div class="space-y-4">
          <div>
            <label class="block text-sm font-medium text-gray-700 mb-2">
              Status Baru
            </label>
            <el-select
              v-model="selectedStatus"
              placeholder="Pilih status baru"
              style="width: 100%"
              size="medium"
              :disabled="statusLoading"
            >
              <el-option
                v-for="status in availableStatuses"
                :key="status.id"
                :label="status.title"
                :value="status.title"
              >
                <!-- <div class="flex flex-col">
                  <span class="font-medium">{{ status.title }}</span>
                  <span class="text-xs text-gray-500 mt-1">{{ status.description }}</span>  
                </div> -->
              </el-option>
            </el-select>
          </div>

          <!-- Reason Input -->
          <div>
            <label class="block text-sm font-medium text-gray-700 mb-2">
              Alasan Perubahan Status
            </label>
            <el-input
              v-model="selectedReason"
              type="textarea"
              placeholder="Masukkan alasan perubahan status (opsional)"
              :rows="3"
              :maxlength="255"
              show-word-limit
              :disabled="statusLoading"
            />
            <p class="text-xs text-gray-500 mt-1">
              Berikan alasan untuk perubahan status ini (maksimal 255 karakter)
            </p>
          </div>

          <!-- Status Description -->
          <div v-if="selectedStatus" class="bg-blue-50 border border-blue-200 rounded-lg p-3">
            <div class="flex items-start space-x-2">
              <Icon icon="mdi:information-outline" class="w-4 h-4 text-blue-600 mt-0.5 flex-shrink-0" />
              <div class="text-sm text-blue-800">
                <p class="font-medium mb-1">Deskripsi Status:</p>
                <p class="leading-relaxed">{{ getStatusDescription(selectedStatus) }}</p>
              </div>
            </div>
          </div>

          <!-- Current vs New Status Comparison -->
          <div v-if="selectedStatus && selectedStatus !== getFieldValue(reportToUpdateStatus, 'status', 'state')" class="bg-gray-50 border border-gray-200 rounded-lg p-3">
            <div class="flex items-center justify-between">
              <div class="text-center">
                <p class="text-xs font-medium text-gray-500 mb-1">Status Saat Ini</p>
                <span 
                  :class="getDetailStatusClass(getFieldValue(reportToUpdateStatus, 'status', 'state'))"
                  class="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium"
                >
                  {{ getFieldValue(reportToUpdateStatus, 'status', 'state') }}
                </span>
              </div>
              <Icon icon="mdi:arrow-right" class="w-5 h-5 text-gray-400" />
              <div class="text-center">
                <p class="text-xs font-medium text-gray-500 mb-1">Status Baru</p>
                <span class="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-green-500 text-white">
                  {{ selectedStatus }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="closeStatusModal" :disabled="statusLoading">
          Batal
        </el-button>
        <el-button 
          type="primary" 
          @click="updateReportStatus" 
          :loading="statusLoading"
          :disabled="!selectedStatus || selectedStatus === getFieldValue(reportToUpdateStatus, 'status', 'state')"
          icon="el-icon-check"
        >
          {{ statusLoading ? 'Mengupdate...' : 'Update Status' }}
        </el-button>
      </div>
    </el-dialog>

    <!-- Evidence Viewer Component -->
    <EvidenceViewer
      :visible.sync="showEvidenceViewer"
      :evidence-data="selectedEvidenceForView"
      @close="handleEvidenceViewerClose"
    />
  </div>
</template>

<script>
import { Icon } from "@iconify/vue2";
import reportsService from '@/services/reportsService';
import evidencesService from '@/services/evidencesService';
import EvidenceViewer from '@/components/EvidenceViewer.vue';
import ingredientData from '@/assets/json/ingredient.json';

export default {
  name: 'AllReports',
  components: { 
    Icon,
    EvidenceViewer
  },
  data() {
    return {
      reports: [],
      loading: false,
      error: null,
      pagination: {
        page: 0,
        size: 10,
        totalElements: 0,
        totalPages: 0,
        first: true,
        last: true,
        empty: true
      },
      sortBy: 'createdAt',
      sortDir: 'desc',
      
      // Detail Modal
      showDetailModal: false,
      selectedReportDetail: null,
      selectedReportId: null,
      detailLoading: false,
      detailError: null,
      
      // Delete Modal
      showDeleteModal: false,
      reportToDelete: null,
      deleteLoading: false,
      deleteConfirmationText: '',
      
      // Evidence functionality
      evidenceDownloading: {},
      
      // Evidence viewer data
      showEvidenceViewer: false,
      selectedEvidenceForView: {},
      
      // Status Update Modal
      showStatusModal: false,
      reportToUpdateStatus: null,
      selectedStatus: '',
      selectedReason: 'Tidak ada',
      statusLoading: false,
      availableStatuses: [],
      
      // Statistics
      statistics: {
        totalActiveReports: 0
      },
      statisticsLoading: false
    }
  },
  computed: {
    currentUser() {
      return this.$store.getters.user || { role: 'user' };
    }
  },
  methods: {
    async fetchReports() {
      // Check if user is admin
      if (this.currentUser.role !== 'admin' && this.currentUser.role !== 'manager') {
        this.error = 'Akses ditolak. Anda tidak memiliki izin untuk melihat semua laporan.';
        return;
      }

      this.loading = true;
      this.error = null;

      try {
        console.log('Fetching all reports for admin...');
        const result = await reportsService.getAllReports(
          this.pagination.page, 
          this.pagination.size, 
          this.sortBy, 
          this.sortDir
        );
        
        console.log('All reports result:', result);
        
        if (result && result.content) {
          this.reports = result.content;
          this.pagination = result.pagination;
          console.log('Reports loaded successfully:', this.reports.length, 'reports');
        } else {
          this.reports = [];
          this.error = 'Data laporan tidak ditemukan';
        }
        
        // Also fetch statistics when refreshing reports
        this.fetchStatistics();
      } catch (error) {
        console.error('Error fetching all reports:', error);
        this.error = error.message || 'Gagal memuat data laporan';
        this.reports = [];
      } finally {
        this.loading = false;
      }
    },

    async fetchStatistics() {
      this.statisticsLoading = true;
      
      try {
        console.log('Fetching reports statistics...');
        const result = await reportsService.getReportsStatistics();
        
        console.log('Statistics result:', result);
        
        if (result && result.success && result.data) {
          this.statistics = result.data;
          console.log('Statistics loaded successfully:', this.statistics);
        } else {
          console.warn('Invalid statistics response:', result);
        }
      } catch (error) {
        console.error('Error fetching statistics:', error);
        // Don't show error message for statistics, just use fallback
      } finally {
        this.statisticsLoading = false;
      }
    },

    getFieldValue(obj, ...fields) {
      for (const field of fields) {
        if (obj && obj[field] !== undefined && obj[field] !== null) {
          return obj[field];
        }
      }
      return '-';
    },

    getStatusClass(status) {
      const statusClasses = {
        'Dibatalkan': 'bg-red-100 text-red-800',
        'Dikirim': 'bg-blue-100 text-blue-800',
        'Disaring': 'bg-yellow-100 text-yellow-800',
        'Investigasi Awal': 'bg-orange-100 text-orange-800',
        'Dilaporkan': 'bg-purple-100 text-purple-800',
        'Investigasi Lanjutan': 'bg-indigo-100 text-indigo-800',
        'Buat Tim': 'bg-teal-100 text-teal-800',
        'Buat Laporan': 'bg-cyan-100 text-cyan-800',
        'Keputusan Terakhir': 'bg-green-100 text-green-800'
      };
      return statusClasses[status] || 'bg-gray-100 text-gray-800';
    },

    getStatusCount(status) {
      const statusLower = status.toLowerCase();
      return this.reports.filter(report => {
        const reportStatus = this.getFieldValue(report, 'status', 'state').toLowerCase();
        return reportStatus === statusLower;
      }).length;
    },

    formatDateTime(dateTime) {
      if (!dateTime) return '-';
      
      try {
        const date = new Date(dateTime);
        if (isNaN(date.getTime())) return dateTime;
        
        return date.toLocaleString('id-ID', {
          year: 'numeric',
          month: '2-digit',
          day: '2-digit',
          hour: '2-digit',
          minute: '2-digit'
        });
      } catch (error) {
        return dateTime;
      }
    },

    changePage(page) {
      this.pagination.page = page - 1; // Convert from 1-based to 0-based
      this.fetchReports();
    },

    viewReportDetail(report) {
      // Navigate to report detail or open modal
      this.$router.push(`/dashboard/reports/${report.id}`);
    },

    // Detail Modal Methods
    async viewReportDetailModal(report) {
      this.selectedReportId = report.id;
      this.showDetailModal = true;
      await this.fetchReportDetail(report.id);
    },

    async fetchReportDetail(reportId) {
      this.detailLoading = true;
      this.detailError = null;
      
      try {
        const result = await reportsService.getReport(reportId);
        this.selectedReportDetail = result.report;
      } catch (error) {
        console.error('Error fetching report detail:', error);
        this.detailError = error.message || 'Gagal memuat detail laporan';
      } finally {
        this.detailLoading = false;
      }
    },

    closeDetailModal() {
      this.showDetailModal = false;
      this.selectedReportDetail = null;
      this.selectedReportId = null;
      this.detailError = null;
      this.detailLoading = false;
    },

    getDetailStatusClass(status) {
      const statusClasses = {
        'Dibatalkan': 'bg-red-500 text-white',
        'Dikirim': 'bg-blue-500 text-white',
        'Disaring': 'bg-yellow-500 text-white',
        'Investigasi Awal': 'bg-orange-500 text-white',
        'Dilaporkan': 'bg-purple-500 text-white',
        'Investigasi Lanjutan': 'bg-indigo-500 text-white',
        'Buat Tim': 'bg-teal-500 text-white',
        'Buat Laporan': 'bg-cyan-500 text-white',
        'Keputusan Terakhir': 'bg-green-500 text-white'
      };
      return statusClasses[status] || 'bg-gray-500 text-white';
    },

    deleteReportFromModal(report) {
      this.closeDetailModal();
      this.deleteReport(report);
    },

    editReportStatus(report) {
      this.reportToUpdateStatus = report;
      this.selectedStatus = this.getFieldValue(report, 'status', 'state') || '';
      this.showStatusModal = true;
    },

    closeStatusModal() {
      this.showStatusModal = false;
      this.reportToUpdateStatus = null;
      this.selectedStatus = '';
      this.selectedReason = 'Tidak ada';
      this.statusLoading = false;
    },

    async updateReportStatus() {
      if (!this.selectedStatus) {
        this.$message.warning('Silakan pilih status terlebih dahulu');
        return;
      }

      this.statusLoading = true;
      
      try {
        await reportsService.updateReportStatus(this.reportToUpdateStatus.id, this.selectedStatus, this.selectedReason);
        
        this.$message.success({
          message: `Status laporan berhasil diubah menjadi "${this.selectedStatus}"`,
          duration: 3000,
          showClose: true
        });
        
        // Update the status in the local data
        const reportIndex = this.reports.findIndex(r => r.id === this.reportToUpdateStatus.id);
        if (reportIndex !== -1) {
          this.$set(this.reports[reportIndex], 'status', this.selectedStatus);
          this.$set(this.reports[reportIndex], 'state', this.selectedStatus);
        }
        
        // Update selected report detail if it's open
        if (this.selectedReportDetail && this.selectedReportDetail.id === this.reportToUpdateStatus.id) {
          this.$set(this.selectedReportDetail, 'status', this.selectedStatus);
          this.$set(this.selectedReportDetail, 'state', this.selectedStatus);
        }
        
        // Close modal
        this.closeStatusModal();
        
      } catch (error) {
        console.error('Error updating report status:', error);
        this.$message.error({
          message: `Gagal mengubah status laporan: ${error.message}`,
          duration: 5000,
          showClose: true
        });
      } finally {
        this.statusLoading = false;
      }
    },

    getStatusDescription(statusTitle) {
      const status = this.availableStatuses.find(s => s.title === statusTitle);
      return status ? status.description : '';
    },

    // Delete Modal Methods
    deleteReport(report) {
      this.reportToDelete = report;
      this.showDeleteModal = true;
      this.deleteConfirmationText = '';
    },

    closeDeleteModal() {
      this.showDeleteModal = false;
      this.reportToDelete = null;
      this.deleteLoading = false;
      this.deleteConfirmationText = '';
    },

    async confirmDeleteReport() {
      if (this.deleteConfirmationText !== 'HAPUS') {
        this.$message.warning('Silakan ketik "HAPUS" untuk mengkonfirmasi penghapusan');
        return;
      }

      this.deleteLoading = true;
      
      try {
        // Call API delete endpoint
        await reportsService.deleteReport(this.reportToDelete.id);
        
        this.$message.success({
          message: `Laporan "${this.getFieldValue(this.reportToDelete, 'name', 'nama', 'title')}" berhasil dihapus`,
          duration: 3000,
          showClose: true
        });
        
        // Close modal and refresh data
        this.closeDeleteModal();
        await this.fetchReports();
        
      } catch (error) {
        console.error('Error deleting report:', error);
        this.$message.error({
          message: `Gagal menghapus laporan: ${error.message}`,
          duration: 5000,
          showClose: true
        });
      } finally {
        this.deleteLoading = false;
      }
    },

    // Evidence functionality methods
    async downloadEvidenceFile(evidence) {
      // Set loading state for specific evidence
      this.$set(this.evidenceDownloading, evidence.id, true);
      
      try {
        console.log('Downloading evidence:', evidence);
        const result = await evidencesService.downloadEvidence(
          evidence.id, 
          evidence.filename || evidence.fileName
        );
        
        if (result.success) {
          this.$notify({
            title: 'Download Berhasil',
            message: `Evidence "${evidence.filename || evidence.fileName}" berhasil didownload`,
            type: 'success',
            duration: 3000,
            position: 'top-right'
          });
        } else {
          this.$message.error(result.error || 'Gagal mendownload evidence');
        }
      } catch (error) {
        console.error('Error downloading evidence:', error);
        this.$message.error('Terjadi kesalahan saat mendownload evidence');
      } finally {
        // Clear loading state
        this.$delete(this.evidenceDownloading, evidence.id);
      }
    },

    // Evidence viewer methods
    viewEvidenceFile(evidence) {
      this.selectedEvidenceForView = evidence;
      this.showEvidenceViewer = true;
    },

    handleEvidenceViewerClose() {
      this.showEvidenceViewer = false;
      this.selectedEvidenceForView = {};
    },

    // Check if file is an image
    isImageFile(filename) {
      if (!filename) return false;
      const imageExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.bmp', '.webp'];
      return imageExtensions.some(ext => filename.toLowerCase().endsWith(ext));
    },

    // Get file icon based on extension
    getFileIcon(filename) {
      if (!filename) return 'mdi:file-outline';
      
      const extension = filename.toLowerCase().split('.').pop();
      switch (extension) {
        case 'pdf':
          return 'mdi:file-pdf-box';
        case 'doc':
        case 'docx':
          return 'mdi:file-word-box';
        case 'jpg':
        case 'jpeg':
        case 'png':
        case 'gif':
        case 'bmp':
        case 'webp':
          return 'mdi:file-image-box';
        case 'mp4':
        case 'avi':
        case 'mov':
          return 'mdi:file-video-box';
        case 'zip':
        case 'rar':
        case '7z':
          return 'mdi:folder-zip-outline';
        default:
          return 'mdi:file-outline';
      }
    }
  },

  mounted() {
    console.log('AllReports component mounted');
    this.availableStatuses = ingredientData.status_pengaduan || [];
    this.fetchReports();
    this.fetchStatistics();
  }
}
</script>

